import React from "react";
import styled, { css } from "styled-components";
import { Input, CheckBox, Button } from "antd";

const TodoBackground = styled.div`
  width: 100vw;
  height: 100vh;
  background: #f7f8f9;
`;
const TodoWrap = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  min-width: 460px;
  min-height: 400px;
  background: #fff;
  box-shadow: 0 0 10px 10px rgba(80, 80, 80, 0.2);
  .todo_title {
    padding: 10px;
    text-align: center;
    font-size: 2rem;
  }
`;
const Todo = () => {
  return (
    <TodoBackground>
      <TodoWrap>
        <h1 className="todo_title">일정 관리</h1>
        <div className="todo_content">
          <ul>
            <li>ggg</li>
            <li>ggg</li>
            <li>ggg</li>
            <li>ggg</li>
          </ul>
        </div>
      </TodoWrap>
    </TodoBackground>
  );
};
export default Todo;
